@media screen and (max-width: 768px) {
  .VscCircleFilled,
  .MdOpenInNew {
    width: 10px;
    height: 10px;
  }
  .swiper {
    position: relative;
    --swiper-theme-color: #000000 !important;
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    scroll-behavior: smooth;
    height: 100vh;
    .swiper-button-prev {
      left: 15% !important;
      top: 75%;
      color: #000000 !important;
    }
    .swiper-button-next {
      right: 15% !important;
      top: 75%;
      color: #000000 !important;
    }
    .swiper-horizontal > .swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal,
    .swiper-pagination-custom,
    .swiper-pagination-fraction {
      bottom: 70px !important;
    }
    .sub_container_section {
      position: relative;
      width: 100% !important;
      height: 100vh !important;
      article {
        position: absolute;
        top: -50px;
        width: 100%;
        height: 100vh;
        .sub_inner {
          .sub_number {
            z-index: 2;
            position: absolute;
            left: 40%;
            top: 100px;
            font-weight: bolder;
            font-size: 100px;
            line-height: 242px;
            text-align: center;
            text-shadow: -5px 5px 4px rgba(0, 0, 0, 0.25);
          }
        }
        .sub_line {
          display: none;
        }
        .sub_circle {
          z-index: 0;
          position: absolute;
          width: 500px;
          height: 500px;
          left: 50%;
          transform: translateX(-50%);
          top: 150px;
          border-radius: 50%;
          background: linear-gradient(227.02deg, rgba(248, 248, 248, 0) 9.35%, rgb(255, 255, 255));
          box-shadow: -16px 19px 18px -5px rgba(0, 0, 0, 0.5);
        }
        .sub_video1 {
          box-sizing: border-box;
          z-index: 1;
          position: absolute;
          width: 450px;
          height: 230px;
          left: 50%;
          top: 300px;
          transform: translateX(-50%);
          border: 14px solid rgb(0, 0, 0);
          backdrop-filter: blur(2px);
          /* Note: backdrop-filter has minimal browser support */
          border-radius: 43px;

          overflow: hidden;
          video {
            width: 100%;
          }
        }
        .sub_video2 {
          box-sizing: border-box;

          position: absolute;
          width: 198px;
          height: 357px;
          left: 50%;
          transform: translateX(-50%);
          top: 270px;
          backdrop-filter: blur(2px);
          /* Note: backdrop-filter has minimal browser support */
          border: 10px solid rgb(0, 0, 0);

          overflow: hidden;
          border-radius: 35px;
          video {
            width: 105%;
            position: absolute;
            top: -6px;
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .sub_video4 {
          box-sizing: border-box;

          position: absolute;
          width: 198px;
          height: 357px;
          left: 50%;
          transform: translateX(-50%);
          top: 270px;
          backdrop-filter: blur(2px);
          /* Note: backdrop-filter has minimal browser support */
          border: 10px solid rgb(0, 0, 0);

          overflow: hidden;
          border-radius: 35px;
          video {
            width: 108%;
            position: absolute;
            top: -11px;
            left: 50%;
            transform: translateX(-50%);
          }
        }
        .sub_video3 {
          box-sizing: border-box;

          position: absolute;
          width: 198px;
          height: 357px;
          left: 50%;
          transform: translateX(-50%);
          top: 270px;
          backdrop-filter: blur(2px);
          /* Note: backdrop-filter has minimal browser support */
          border: 10px solid rgb(0, 0, 0);

          overflow: hidden;
          border-radius: 35px;
          video {
            position: absolute;
            left: 50%;
            top: -15px;
            transform: translateX(-50%);
            width: 110%;
          }
        }

        .sub_content {
          padding: 20px 25px;
          gap: 25px;
          width: 70%;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          bottom: 100px;
          background: rgba(255, 255, 255, 0.5);
          box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
          backdrop-filter: blur(2px);
          border-radius: 10px;
          h3 {
            text-align: center;
            font-weight: 900;
            font-size: 30px;
            span {
              position: relative;
              top: -10px;
              padding: 5px;
              margin-left: 10px;
              width: 48px;
              height: 30px;
              font-size: 10px;
              background: #000000;
              border-radius: 5px;
              color: #fff;
            }
          }
          .sub_date {
            text-align: center;
            font-weight: bold;
            margin: 5px 0;
          }
          .sub_lang {
            justify-content: center;
            display: flex;
            flex-direction: row;
            padding: 0;
            margin: 20px 0 0 0;
            li {
              padding: 10px;
              font-size: 10px;
              color: black;
              border: 1px solid black;
              border-radius: 5px;
              margin-right: 10px;
            }
          }
          .sub_cont {
            display: none;
          }
        }
      }
      .sub_btn {
        position: absolute;
        bottom: 100px;
        width: 100%;
        .sub_btn_1 {
          display: flex;
          justify-content: center;
          flex-direction: row;
          height: 35px;
          button {
            a {
              text-decoration: none;
              color: #fff;
              padding: 10px;
            }
            cursor: pointer;
            margin-left: 10px;

            background: #000000;
            border-radius: 5px;
            color: #fff;
          }
        }
      }
    }

    .sub__bg {
      position: fixed;
      z-index: -1;
      top: 0;
      width: 100%;
      height: 100%;
      background: conic-gradient(from 207.88deg at 58.47% 83.98%, #afafaf 0deg, rgba(231, 231, 231, 0) 360deg), #ffffff;
      filter: blur(1px);
    }
  }
}
